@import url(https://fonts.googleapis.com/css2?family=Courgette&display=swap);
body {
  background: linear-gradient(to right, rgb(255, 255, 255),  rgb(230, 210, 249), rgb(255, 255, 255));
}

h1 {
  font-family: 'Courgette', cursive;
  font-size: 50px;
  text-align: center;
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
}

.header {
  text-align: center;
  margin-bottom: 15px;
  font-family: 'Courgette', cursive;
  font-size: 23px
}

.group-1,
.group-2,
.group-3 {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}

.group-1 {
  margin-top: 15vh;
}

.group-2,
.group-3 {
  margin-top: 10vh;
}

.group-3 {
  margin-bottom: 10vh;
}
img {
  width: 300px;
  height: 400px;
  border: 1px groove black;
  padding: 20px;
  box-shadow: 2px 2px 3px #000000,
              -2px -2px 3px #000000
  ;
}

button {
  width: 100px;
  height: 40px;
  margin-top: 10px;
  outline: none;
  -webkit-appearance: none;
          appearance: none;
  border: 1px solid black;
  border-radius: 15px;
  font-size: 15px;
  background: #ffffff;
}

button:hover {
  box-shadow: 2px 2px 10px #bebcc2,
              -2px -2px 10px #bebcc2
  ;
  background-color: #e5d9fa;
  cursor: pointer;
}

.content {
  text-align: center;
}

